import React from 'react'
import {openRef, getStatic} from '../helpers'

interface BioBlockProps {
    shareLink: string,
}

const BioBlock = (props:BioBlockProps) => {
    const { shareLink } = props
    return (
        <div className="bio-block mt-5">
            <div className="row">
                <div className="col-2"></div>
                <div className="col-10">
                    <h2>
                        Biography of Ukraine written by Heroes
                    </h2>
                </div>
            </div>
            <div className="row mt-5">
                <div className="col-5 offset-4">
                    <span className="lead">Have the stories worth to be told?</span>
                    <br />
                    <span className="lead">Let us know.</span>
                </div>
            </div>

            <div className="row my-5">
                <div className="col-4 offset-2">
                    <button type="button" className="btn btn-nftua buy-btn px-5 lead" onClick={() => openRef(shareLink)}>
                        <span className="lead"><strong>Share the story</strong></span> 
                        <span className="mx-1 lead">&#10095;</span>
                    </button>
                </div>
                <div className="col-5" style={{overflow:'hidden'}}>
                    <img src={getStatic('eu-m')}/>
                </div>
            </div>

        </div>
    )
}
export default BioBlock