import React from 'react'

export const openRef = (ref, newTab=false) => {
    window.open(ref, '_blank')
}

export const createComponent = (tagName:string, props, content) => {
    return React.createElement(
            'div',
            {className:'content-element-wrapper'},
            React.createElement(
                tagName,
                props,
                content
            )
        )
}
const staticPath = '/heroesua.com'
const staticMap = {
    'logo': `${staticPath}/static/img/logo.png`,
    'telega': `${staticPath}/static/img/telega.png`,
    'discord': `${staticPath}/static/img/discord.png`,
    'opensea': `${staticPath}/static/img/open_sea.png`,
    'right-arrow': `${staticPath}/static/img/right-arrow.png`,
    'eu-m': `${staticPath}/static/img/eu-m.png`,
    '3zub': `${staticPath}/static/img/3zub.png`,
}

export const getStatic = (name:string):string => {
    return staticMap[name]
}