import React from 'react'
import { Link } from 'react-router-dom'
import { getStatic } from '../helpers'
import { LinkType } from '../types'
// import LeftMenu from './LeftMenu'


interface MainHeadlineProps {
    menuItems: LinkType[]
}

const MainHeadline = (props:MainHeadlineProps) => {

    const { menuItems } = props

    return (
        <div className="main-block row px-5">

            <ul className="nav flex-column col-md-3 left-side-bar d-none d-md-block">
                {menuItems.map((item, index) => {
                    return (
                        <li className="nav-item" key={`left-navbar-item-${index}`}>
                            <Link className="nav-link" to={item.url}>{item.name}</Link>
                        </li>
                    )
                })}
            </ul>

            <div className="col-12 col-sm-8 col-md-8 col-lg-6 px-0">
                <h1 className="main-heading mb-5 d-none d-lg-block">Stories of Heroes</h1>
                <h1 className="main-heading smaller mb-5 d-lg-none">Stories of Heroes</h1>
                <p className="col-12 col-md-7 col-lg-5 mb-5 main-subheading">since February 24 to this day</p>
                <br />
                <p className="mt-3">
                    The Ukraine War project is the history of the Ukrainian war in actions and portraits of modern heroes. We aim to show the faces of the country through specific examples of extraordinary people so that this fight won't be erased from our memory along with the news.
                </p>
                <p className='mt-3'>
                    Ukraine War is a live encyclopedia that's developing together with our country. People tend to forget events, but they do remember people. That's why we're focused on personalities. This way, everyone, regardless of time and location, could form a personal connection and remember. 
                </p>
                <p className='mt-3 mb-5'>
                    So that when this war is over, our story of victory will be the biography of Ukraine written in heroes, not ordinary statistics. 
                </p>
                <br />
            </div>

            <div className="col position-relative d-none d-md-block">
                <div className="zub3-bg">
                    <img src={getStatic('3zub')} alt="" />
                </div>
            </div>

        </div>
    )
}

export default MainHeadline